.modal {
	display: none;
	position: fixed;
	inset: 0;

	justify-content: center;
	align-items: center;

	background: hsla(0, 0%, 0%, 0.5);
}

.modal.active {
	display: flex;
}

.modalContent {
	width: 360px;

	display: flex;
	flex-direction: column;
	padding: 16px;

	border-radius: 8px;
	background: white;
}

.modalContent-enter {
	opacity: 0;
	transform: scale(0.9);
}

.modalContent-enter-active {
	transform: scale(1);
	opacity: 1;
	transition: all 500ms ease-out;
}

.modalContent-exit {
	opacity: 1;
}

.modalContent-exit-active {
	opacity: 0;
	transform: scale(0.9);
	transition: all 500ms ease-out;
}