/* RESET */

/* 1. Use a more-intuitive box-sizing model. */
*,
*::before,
*::after {
	box-sizing: border-box;
}

/* 2. Remove default margin */
* {
	margin: 0;
}

/* 3. Allow percentage-based heights in the application */
html,
body {
	height: 100%;
}

/* Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering */
body {
	line-height: 1;
	-webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img,
picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

/* 7. Remove built-in form typography styles */
input,
button,
textarea,
select {
	font: inherit;
}

/* 8. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

/* 9. Create a root stacking context */
#root,
#__next {
	isolation: isolate;
}



/* Layout */

body {
	background: #f0f0f0;
}

#root {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;

	font-family: sans-serif;
	font-size: 16px;
}

.App {
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px;
	align-items: center;
}

.postlist {
	width: 100%;
	max-width: 640px;
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px;
}

.posts {
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px;
}


.post {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 16px;
	gap: 8px;

	background: white;
	border-radius: 8px;
}

.post .content {
	flex: 1 0 0;
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.post .actions {
	display: flex;
	flex-direction: row;
	gap: 8px;
}

.postform {
	width: 100%;
	max-width: 640px;
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px;
}

.filters {
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 8px;
}

.header {
	width: 100%;
	max-width: 640px;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 8px;
	border-bottom: 1px solid lightgray;
}



.post-enter {
	opacity: 0;
	transform: scale(0.9);
}

.post-enter-active {
	transform: scale(1);
	opacity: 1;
	transition: all 200ms ease-out;
}

.post-exit {
	opacity: 1;
}

.post-exit-active {
	opacity: 0;
	transform: scale(0.9);
	transition: all 200ms ease-out;
}

.postPage {
	align-self: center;
	width: 100%;
	max-width: 640px;

	display: flex;
	flex-direction: column;
	padding: 8px 0;
	gap: 8px;
}

.postBody,
.postComments {
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 16px;

	border-radius: 8px;
	background: white;
}

.postComments>h5 {
	color: gray;
}

.postComment {
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px 0;
	border-bottom: 1px solid lightgray;
}

.postComment:last-child {
	border-bottom: none
}

.loginPage {
	align-self: center;
	flex: 1 0 0;
	width: 100%;
	max-width: 360px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 16px 0;
	gap: 16px;
}

.loginPage form {
	display: flex;
	flex-direction: column;
	gap: 8px;
}